<template>
  <div
    class="good-item"
    @click="$router.push('/pdd/detail?id=' + good.goods_sign+'&searchId='+good.search_id)"
  >
    <div class="good-item-flex">
      <div class="good-img">
        <img :src="good.goods_thumbnail_url" />
      </div>
      <div class="good-info">
        <div class="title">{{ good.goods_name }}</div>
        <div class="coupon-info">
          <div class="item">券￥{{ good.coupon_discount / 100 }}</div>
          <div class="item">已售{{ good.sales_tip }}件</div>
        </div>
        <div class="price-content">
          <div class="price">
            劵后￥{{ (good.min_normal_price - good.coupon_discount) / 100 }}
          </div>
          <div class="original-price">￥{{ good.min_normal_price / 100 }}</div>
        </div>
        <div class="btn" @click.stop="doCopy(good)">
          <!-- 赚￥{{ promotion_rate }} -->
          前往购买
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  props: {
    good: Object,
  },
  data() {
    return {};
  },
  computed: {
    ...mapState(["rate"]),
    promotion_rate() {
      return (
        ((this.good.promotion_rate / 1000) *
          (this.good.min_normal_price / 100) *
          this.rate) /
        100
      ).toFixed(2);
    },
  },
  methods: {
    // 复制链接
    doCopy(item) {
      this.$router.push({
        path: "/pdd/share",
        query: {
          id: item.goods_sign,
          searchId: item.search_id,
        },
      });
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" rel="stylesheet/less" scoped>
.good-item {
  padding: 5px 10px;
  .good-item-flex {
    border-radius: 10px;
    background: #ffffff;
    display: flex;
    position: relative;
    .good-img {
      width: 96px;
      img {
        width: 96px;
        border-radius: 10px 0 0 10px;
      }
    }
    .good-info {
      flex: 1;
      padding: 5px 8px;
      .title {
        color: #000;
      }
      .coupon-info {
        display: flex;
        padding: 4px 0;
        .item {
          border-radius: 4px;
          padding: 0 6px;
          font-size: 10px;
          border: 1px solid #ee0a24;
          color: #ee0a24;
          margin-right: 4px;
        }
      }
      .price-content {
        .price {
          color: #ee0a24;
        }
        .original-price {
          font-size: 10px;
          color: #999;
          text-decoration: line-through;
        }
      }
      .btn {
        position: absolute;
        padding: 0 15px;
        background: #ee730a;
        color: #ffffff;
        font-size: 14px;
        border-radius: 16px 0 0 16px;
        height: 32px;
        line-height: 32px;
        cursor: pointer;
        right: 0;
        bottom: 10px;
      }
    }
  }
}
</style>



