<template>
  <div class="good-detail-box pdd-detail-box">
    <van-nav-bar title="拼多多商品详情" fixed left-arrow @click-left="$router.go(-1)" />
     <van-swipe :autoplay="3000">
      <van-swipe-item v-for="(image, index) in images" :key="index">
        <div
          class="good-img"
          :style="{ 'background-image': 'url(' + image + ')' }"
        ></div>
      </van-swipe-item>
    </van-swipe>

    <div class="good-body">
      <div class="price-content">
        <div class="coupon-price">券后价：￥<span class="price">{{ goodInfo.min_normal_price / 100 }}</span></div>
        <!-- <div class="original-price">原价:￥{{}}</div> -->
        <div class="volume">
          已拼 <span class="c-red">{{ goodInfo.sales_tip }}</span> 件
        </div>
      </div>
      <div class="name-content">
        <span class="name">
          {{ goodInfo.goods_name }}
        </span>
      </div>
      <div class="coupon-content" v-if="goodInfo.has_coupon">
        <van-tag plain size="large" type="danger">{{goodInfo.coupon_discount/100}}元券剩余{{goodInfo.coupon_remain_quantity}}张</van-tag>
      </div>
      <div class="commission-content">
        <van-icon name="after-sale" /> 当前佣金
        <span class="commission">￥{{promotion_rate}} </span> 元
      </div>
      <div class="info"><span v-for="(item,index) in goodInfo.unified_tags" :key="index" style="padding-right:15px;">{{item}}</span></div>
    </div>
    <div style="height: 55px"></div>
    <van-goods-action>
      <van-goods-action-icon
        icon="shop-o"
        text="首页"
        @click="$router.push('/')"
      />
      <!-- <van-goods-action-button
        type="warning"
        :text="`分享赚 ${promotion_rate} 元`"
        @click="doCopy"
      />
      <van-goods-action-button
        type="danger"
        :text="`立即买省 ${promotion_rate} 元`"
        @click="doCopy"
      /> -->
       <van-goods-action-button
        type="warning"
        :text="`分享`"
        @click="doCopy"
      />
      <van-goods-action-button
        type="danger"
        :text="`立即买`"
        @click="doCopy"
      />
    </van-goods-action>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      goodInfo: {}, //商品详情
      images: [], //轮播图
    };
  },
   computed: {
    ...mapState(["rate"]),
    // 佣金折算
     promotion_rate() {
      return (
        ((this.goodInfo.promotion_rate / 1000) *
          (this.goodInfo.min_normal_price / 100) *
          this.rate) /
        100
      ).toFixed(2);
    },
  },
  created() {
    this.Post({ goodSign: this.$route.query.id,searchId:this.$route.query.searchId}, "/pdd/goodsDetail", (res) => {
      if (res.states == 1) {
        let Data = res.data.goods_detail_response.goods_details[0];
        this.goodInfo = Data;
        this.images = Data.goods_gallery_urls;
      }
    });
  },
  methods: {
    // 复制链接
    doCopy() {
      this.$router.push("/pdd/share?id=" + this.$route.query.id,this.$route.query.searchId);
    },
  },
};
</script>

<style lang="less" rel="stylesheet/less" scoped>
@import '../../assets/less/good.less';
</style>



